import React, { Component } from 'react';
import HorizontalEdge from './edge';
import { Stage, Layer, Rect, Text } from 'react-konva';
import VerticalEdge from './VertialEdge';
import { Alert } from 'react-bootstrap';
class Square extends Component {
    
    constructor(props) {
        super(props)

        this.state = { 
            SIZE: 49,
            x: 100,
            y: 15,
            N: [],
            S: [],
            W: [],
            E: [],
            res: [],
            dispaly: [],
        };
        for(let i = 0; i < 7; i++) {
            for(let j = 0; j < 7; j++) {
                this.state.W.push({i: i, j: j, color: 'lightgrey'})
                this.state.S.push({i: i, j: j, color: 'lightgrey'})
                this.state.N.push({i: i, j: j, color: 'lightgrey'})
                this.state.E.push({i: i, j: j, color: 'lightgrey'})
            }
        }
    }

    componentDidMount() {
        this.getRes();
    }

    getRes() {
        let res = this.state.res.slice();
        for(let i = 0; i < 7; i++) {
            res[i] = ''
            for(let j = 0; j < 7; j++) {
                let t = 0, pos = i * 7 + j;
                if(this.state.N[pos].color === 'orange') {
                    t += 0b1000;
                }
                if(this.state.E[pos].color === 'orange') {
                    t += 0b0100;
                }
                if(this.state.S[pos].color === 'orange') {
                    t += 0b0010;
                }
                if(this.state.W[pos].color === 'orange') {
                    t += 0b0001;
                }
                res[i] += t.toString(16)
            }
        }
        this.setState({res: res,
            display: "'" + res.join("','") + "'"
        })
    }

    handleClick(i, j, direction) {
        let pos = i * 7 + j;
        if(direction === 'N') {
            let N = [...this.state.N];
            if(N[pos].color === 'lightgrey')
                N[pos].color = 'orange'
            else
                N[pos].color = 'lightgrey'
            this.setState({N})
        } else if(direction === 'S') {
            let S = [...this.state.S];
            if(S[pos].color === 'lightgrey')
                S[pos].color = 'orange'
            else
                S[pos].color = 'lightgrey'
            this.setState({S})
        } else if(direction === 'W') {
            let W = [...this.state.W];
            if(W[pos].color === 'lightgrey')
                W[pos].color = 'orange'
            else
                W[pos].color = 'lightgrey'
            this.setState({W})
        } else if(direction === 'E') {
            let E = [...this.state.E];
            if(E[pos].color === 'lightgrey')
                E[pos].color = 'orange'
            else
                E[pos].color = 'lightgrey'
            this.setState({E})
        }
        this.getRes();
        // let items = [...this.state.items]
        // // let color = items[i * 7 + j] === 'lightgrey' ? 'orange' : 'lightgrey'
        // items[i * 7 + j].color = 'orange'
        // this.setState(
        //     {items}
        // )
    }


    render() { 
        return (
            <React.Fragment>
            [
            {/* {this.state.res.map((n) => {
                return('\"' + n + '\"' + ' ,');
            })} */}
            {this.state.display}
            ]
            <Stage width={window.innerWidth} height={window.innerHeight}>
                <Layer>
                    <React.Fragment>
                        {this.state.N.map((obj) => {
                            return (
                                <HorizontalEdge x={this.state.x + obj.j * 100 + 15} y={this.state.y + obj.i * 100} color={obj.color} onClick={() => this.handleClick(obj.i, obj.j, 'N')} key={obj.i * 7 + obj.j}></HorizontalEdge>
                            );
                        })}
                        {this.state.S.map((obj) => {
                            return (
                                <HorizontalEdge x={this.state.x + obj.j * 100 + 15} y={this.state.y + obj.i * 100 + 100} color={obj.color} onClick={() => this.handleClick(obj.i, obj.j, 'S')} key={(obj.i * 7 + obj.j) + 400}></HorizontalEdge>
                            );
                        })}

                        {this.state.W.map((obj) => {
                            return (
                                <VerticalEdge x={this.state.x + obj.j * 100} y={this.state.y + obj.i * 100 + 15} onClick={() => this.handleClick(obj.i, obj.j, 'W')} color={obj.color}></VerticalEdge>
                            );
                        })}

                        {this.state.E.map((obj) => {
                            return (
                                    <VerticalEdge x={this.state.x + obj.j * 100 + 100} y={this.state.y + obj.i * 100 + 15} onClick={() => this.handleClick(obj.i, obj.j, 'E')} color={obj.color}></VerticalEdge>
                            );
                        })}
                    </React.Fragment>
                </Layer>
            </Stage>
            </React.Fragment>
        );
    }
}
 
export default Square;